import { InputText, InputTextProps } from 'primereact/inputtext';
import React from 'react';
import cx from 'classnames';

interface TextInputProps extends InputTextProps {
    left?: React.ReactElement;
    right?: React.ReactElement;
    label?: string;
    error?: string;
    help?: string;
}

const TextInput = React.forwardRef<InputText, TextInputProps>(
    ({ left, label, error, help, right, ...inputProps }, ref) => {
        const [type, setType] = React.useState<React.HTMLInputTypeAttribute>(inputProps.type ?? 'text');

        const rightAddon = React.useMemo(() => {
            if (!right && inputProps.type === 'password') {
                return (
                    <i
                        className={cx('pi', { 'pi-eye': type === 'password', 'pi-eye-slash': type === 'text' })}
                        onClick={() => setType(type === 'text' ? 'password' : 'text')}
                    ></i>
                );
            }
            return right;
        }, [type, inputProps.type, right]);

        return (
            <>
                <div className={cx({ 'p-inputgroup': left || rightAddon })}>
                    {left && <div className="p-inputgroup-addon">{left}</div>}
                    {label && (
                        <label htmlFor="username2" className="block">
                            {label}
                        </label>
                    )}
                    <InputText
                        className={cx(inputProps.className, 'block', { 'p-error': error })}
                        {...inputProps}
                        type={type}
                        ref={ref}
                    />
                    {rightAddon && <div className="p-inputgroup-addon">{rightAddon}</div>}
                </div>
                {(error || help) && <small className={cx('block', { 'p-error': error })}>{error || help}</small>}
            </>
        );
    },
);

export default TextInput;
