import React from 'react';
import { Outlet, useNavigate } from 'react-router-dom';
import { TabMenu } from 'primereact/tabmenu';
import { useLocation } from '../../hooks/use-location';
import { MenuItemCommandParams } from 'primereact/menuitem';

interface BaseLayoutProps {
    onSignOut: () => void;
}

export const BaseLayout: React.FC<BaseLayoutProps> = ({ onSignOut }) => {
    const location = useLocation();

    const navigate = useNavigate();

    const navigateCommand = ({ item, originalEvent }: MenuItemCommandParams) => {
        originalEvent.preventDefault();
        navigate(item.url!);
    };

    const items: React.ComponentProps<typeof TabMenu>['model'] = [
        { label: 'Home', icon: 'pi pi-fw pi-home', url: '/logs', command: navigateCommand },
        { label: 'Settings', icon: 'pi pi-fw pi-cog', url: '/settings', command: navigateCommand },
        { label: 'Sign out', icon: 'pi pi-fw pi-sign-out', className: 'ml-auto', command: onSignOut },
    ];

    const activeIndex = items.findIndex((item) => item.url && location.pathname.startsWith(item.url));

    return (
        <div>
            <TabMenu model={items} activeIndex={activeIndex} />
            <Outlet />
        </div>
    );
};
